<template>
  <div class="recommend" ref="recommend">
    <scroll ref="scroll" class="recommend-content" :data="discList">
      <div>
        <div v-if="sliderData.length > 0" class="slider-wrapper">
          <!-- 引入slider组件 -->
          <slider>
            <div v-for="item in sliderData" :key="item.id">
              <a :href="item.linkUrl">
                <img class="needsclick" @load="loadImage" :src="item.picUrl" alt="" />>
              </a>
            </div>
          </slider>
        </div>
        <div class="recommend-list">
          <h1 class="list-title">热门歌单推荐</h1>
          <ul class="discList">
            <li class="item" @click="selectItem(item)" v-for="(item, index) in discList" :key="index">
              <div class="icon">
                <img v-lazy="item.imgurl" width="60" height="60" alt="">
              </div>
              <div class="text">
                <h2 class="name">{{item.creator.name}}</h2>
                <p class="desc">{{item.dissname}}</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!discList.length">
        <loading></loading>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script>
import { getRecommend } from "../../api/recommend";
import { OK } from "../../api/config";
import Slider from "../../base/slider";
import discListData from "../../data/discList.json";
import Scroll from "../../base/scroll.vue";
import Loading from "../../base/loading.vue";
import {mapMutations} from 'vuex'
import {playListMixin} from '../../common/tools/mixin.js'

export default {
  mixins: [playListMixin],
  data() {
    return {
      sliderData: [], //轮播图数据
      discList: []
    };
  },
  components: {
    Slider,
    Scroll,
    Loading
  },
  created() {
    this._getRecommend();
    setTimeout(()=>{
      this._getDiscList();
    },1000)
  },
  methods: {
    // 获取轮播图数据
    _getRecommend() {
      getRecommend().then(res => {
        // 如果数据成功返回
        if (res.code === OK) {
          this.sliderData = res.data.slider;
        }
      });
    },
    // 获取歌单数据
    _getDiscList() {
      this.discList = discListData.data.list;
      // console.log(discListData)
    },
    loadImage() {
      if (!this.checkLoaded) {
        this.$refs.scroll.refresh();
        this.checkLoaded = true;
      }
    },

    selectItem(item) {
      this.$router.push({
        path: `/recommend/${item.dissid}`
      })

      this.setDisc(item)
    },

    ...mapMutations({
      setDisc: 'SET_DISC'
    }),

    handlePlayList(playList)  {
      let bottom = playList.length > 0 ? '60px' : 0 
      this.$refs.recommend.style.bottom = bottom
      this.$refs.scroll.refresh()
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../common/style/variable.less";

.recommend {
  position: fixed;
  width: 100%;
  top: 88px;
  bottom: 0;

  .recommend-content {
    height: 100%;
    overflow: hidden;

    .slider-wrapper {
      position: relative;
      width: 100%;
      overflow: hidden;
    }

    .recommend-list {
      .list-title {
        height: 65px;
        line-height: 65px;
        text-align: center;
        font-size: @font-size-medium;
        color: @color-theme;
      }

      .discList {
        .item {
          display: flex;
          padding: 0 20px 20px 20px;
          box-sizing: border-box;
          align-items: center;

          .icon {
            flex-basis: 60px;
            margin-right: 20px;
          }

          .text {
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .name {
              color: @color-text;
              margin-bottom: 10px;
            }

            .desc {
              color: @color-text-d;
            }
          }
        }
      }
    }

    .loading-container {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>


