import * as types from "./mutation-types";

const mutations = {
    //类似于注册一个SET_SINGER事件，组件可以通过this.$store.commit('SET_SINGER',singer)来进行修改
    [types.SET_SINGER](state, singer) {    
        state.singer = singer
    },
    [types.SET_PLAYING_STATE](state, flag) {       //播放状态
        state.playing = flag
    },
    [types.SET_FULL_SCREEN](state, flag) {         //全屏播放
        state.fullScreen = flag
    },
    [types.SET_PLAY_LIST](state, list) {           //播放列表
        state.playList = list
    },
    [types.SET_SEQUENCE_LIST](state, list) {        //顺序播放列表
        state.sequencrList = list
    },
    [types.SET_PLAY_MODE](state, mode) {            //播放模式
        state.mode = mode
    },
    [types.SET_CURRENT_INDEX](state, index) {       //当前索引
        state.currentIndex = index
    },
    [types.SET_DISC](state,disc) {
        state.disc = disc
    },
    [types.SET_TOP_LIST](state, topList) {
        state.topList = topList
    },
}

export default mutations