<template>
  <scroll class="suggest" :data="result" :pullup="true" @scrollToEnd="searchMore"
  ref="suggest">
    <ul class="suggest-list">
      <li class="suggest-item" @click="selectItem(item)" v-for="(item, index) in result" :key="index">
        <div class="icon">
          <i :class="getIconCls(item)"></i>
        </div>
        <div class="name">
          <p class="text" v-html="getDiscplayName(item)"></p>
        </div>
      </li>
      <loading v-show="hasMore" title=""></loading>
    </ul>
  </scroll>
</template>

<script>
import { search } from "../../api/search.js";
import { OK } from "../../api/config.js";
import Scroll from "../../base/scroll";
import Loading from "../../base/loading";
import Singer from "../../common/tools/singer.js"
import {mapMutations,mapActions} from 'vuex'

const TYPE_SINGER = "singer";
const PAGE_NUM = 20; //设置每次请求返回歌曲的个数

export default {
  props: {
    query: {
      type: String,
      default: ""
    },
    showSinger: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      page: 1, //第几页
      result: [],
      hasMore: true //更多数据
    };
  },
  components: {
    Scroll,
    Loading
  },
  methods: {
    search() {
      this.page = 1;
      this.hasMore = true;
      this.$refs.suggest.scrollTo(0,0)      //移动到顶部
      if (this.query === "") return;
      search(this.query, this.page, PAGE_NUM).then(res => {
        if (res.code === OK) {
          // 要先对数据进行处理
          this.result = this._genResult(res.data);
          this._checkMore(res.data); //判断是否还有跟多数据
        }
      });
    },

    searchMore() {
      if (!this.hasMore) return;
      this.page++;
      search(this.query, this.page, PAGE_NUM).then(res => {
        if (res.code === OK) {
          // 把新数据添加到原来的数据
          this.result = this.result.concat(this._genResult(res.data));
          this._checkMore(res.data);
        }
      });
    }, 

    //跳转到子路由
    selectItem(item) {
      if (item.type === TYPE_SINGER) {
        // 如果是歌手就实例化一个歌手对象
        const singer = new Singer({
          id:item.singerMID,
          name: item.singerName
        })
        this.$router.push({
          path: `/search/${singer.id}`
        })
        this.setSinger(singer)
      }else {
        // 对返回的数据进行处理
        let obj = {}
        obj.name = item.name
        obj.singer = item.singer[0].name
        obj.album = item.album.name
        obj.image = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${item.singer[0].mid}.jpg?max_age=2592000`
        obj.duration = 215
        obj.url = 'http://dl.stream.qqmusic.qq.com/C400003OUlho2HcRHC.m4a?guid=1546862306&vkey=E64D3C27D1583641B0AD8240C9AC45BF9D274B016C4CB8070BD3019584FAEC049A8D5674AF3BA932384614688F2C0A215C43C7B92C9DD206&uin=0&fromtag=38'

        this.insertSong(obj)
      }
    },

    ...mapMutations({
      setSinger: 'SET_SINGER'
    }),
    ...mapActions([
      'insertSong'
    ]),

    //对返回的数据进行处理
    _genResult(data) {
      let ret = [];
      if (data.zhida && data.zhida.type === 1) {
        // 有歌手信息的时候就添加这条数据
        ret.push({ ...data.zhida.zhida_singer, ...{ type: TYPE_SINGER } });
      }
      if (data.song) {
        ret = ret.concat(data.song.list);
      }
      return ret;
    },

    refresh() {
      this.$refs.suggest.refresh
    },

    _checkMore(data) {
      const song = data.song;
      // 如果歌曲数据为空 或者当前的数量*当前的页码 大于等于 总数量
      if (!song.list.length && song.curnum * song.curpage >= song.totalnum) {
        this.hasMore = false;
      }
    },

    //根据数据里的type来判断是歌手还是歌曲  分别添加不同的类名
    getIconCls(item) {
      if (item.type === TYPE_SINGER) {
        return "icon-mine";
      } else {
        return "icon-music";
      }
    },

    //由于数据不同， 获取歌曲名的方法也不同
    getDiscplayName(item) {
      if (item.type === TYPE_SINGER) {
        return item.singerName;
      } else {
        return `${item.name}-${item.singer[0].name}`;
      }
    }
  },
  watch: {
    query() {
      this.search();
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../common/style/variable.less";

.suggest {
  height: 100%;
  overflow: hidden;

  .suggest-list {
    padding: 0 30px;

    .suggest-item {
      display: flex;
      align-items: center;
      padding-bottom: 20px;

      .icon {
        flex: 0 0 30px;
        width: 30px;

        [class^="icon-"] {
          width: 16px;
          height: 16px;
          display: block;
          background-size: 100%;
        }

        .icon-mine {
          background-image: url("../../common/image/singer.png");
        }

        .icon-music {
          background-image: url("../../common/image/music.png");
        }
      }

      .name {
        flex: 1;
        font-size: @font-size-medium;
        color: @color-text-d;
        overflow: hidden;

        .text {
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
      }
    }

    .no-result-wrapper {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>


