<template>
    <div class="search-box">
        <i class="icon-search"></i>
        <input class="box" type="box" v-model="query" :placeholder="placeholder">
        <i @click='del' v-show="query" class="icon-dismiss"></i>
    </div>
</template>

<script>
export default {
  props: {
    placeholder: {
      type: String,
      default: "搜索歌曲、歌手"
    }
  },
  data() {
    return {
      query: ""
    }
  },
  methods: {
    //  清空搜索栏数据
    del() {
        this.query = ''
    },
    setQuery(query) {
        this.query = query
    }
  },
  created() {
      //当query改变的时候就会派发一个事件
      this.$watch('query', (newQuery)=>{
          this.$emit('query',newQuery)
      })
  }
};
</script>

<style lang="less" scoped>
@import "../common/style/variable.less";

.search-box {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  padding: 0 6px;
  height: 40px;
  background: @color-highlight-background;
  border-radius: 6px;

  .search-icon {
    font-size: 24px;
    color: @color-background;
  }

  .icon-search {
    display: inline-block;
    width: 20px;
    height: 20px;
    background-image: url("../common/image/search.png");
    background-size: 100%;
    border-radius: 50%;
  }

  .box {
    flex: 1;
    margin: 0 5px;
    line-height: 18px;
    background: @color-highlight-background;
    color: @color-text-s;
    font-size: @font-size-medium;

    &::placeholder {
      color: @color-text-d;
    }
  }

  .icon-dismiss {
    display: inline-block;
    width: 20px;
    height: 20px;
    background-image: url("../common/image/cha.png");
    background-size: 100%;
    border-radius: 50%;
  }
}
</style>


