
export default class Song {
    //singer 歌手     name 歌曲名称       album 专辑名称
    //duration 歌曲长度     image歌曲图片       url:歌曲的请求路径
    constructor ({id,mid,singer,name,album,duration,image,url}) {
        this.id = id,
        this.mid = mid,
        this.singer = singer,
        this.name = name,
        this.album = album,
        this.duration = duration,
        this.image = image,
        this.url = url
    }
}


export function createSong(musicData) {
    return new Song ({
        id: musicData.songid,
        mid: musicData.songmid,
        singer: filterSinger(musicData.singer),
        name: musicData.songname,
        album: musicData.albumname,
        duration: musicData.interval,
        image: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`,
        url: `http://dl.stream.qqmusic.qq.com/C400003OUlho2HcRHC.m4a?guid=1546862306&vkey=E64D3C27D1583641B0AD8240C9AC45BF9D274B016C4CB8070BD3019584FAEC049A8D5674AF3BA932384614688F2C0A215C43C7B92C9DD206&uin=0&fromtag=38`
    })
}

function filterSinger(singer) {
    let ret = []
    if (!singer) {
        return ''
    }

    singer.forEach(s=> {
        ret.push(s.name)
    })

    return ret.join('/')
}