<template>
  <scroll class="listview" :data="data" ref="listview" :listenScroll="listenScroll" :probeType="probeType" @scrollMove="scroll">
    <ul>
      <li class="list-group" v-for="(group,i) in data" :key="i" ref="listGroup">
        <h2 class="list-group-title">{{group.title}}</h2>
        <ul>
          <li class="list-group-item" v-for="(item,index) in group.items"  :key="index" @click="selectItem(item)">
            <img class="avater" v-lazy="item.avater" alt="">
            <span class="name">{{item.name}}</span>
          </li>
        </ul>
      </li>
    </ul>
    <!-- 右侧快速入口 -->
    <div class="list-shortcut" @touchstart="touchMoveTo" @touchmove="onTouchMove">
      <ul>
        <li class="item" :class="{active: currentIndex===index}" v-for="(item,index) in shortCutList" :key="index" :data-index="index">
          {{item}}
        </li>
      </ul>
    </div>
    <!-- 固定导航栏 -->
    <div class="list-fixed" v-show="fixedTitle">
      <h1 class="fixed-title">{{fixedTitle}}</h1>
    </div>

  </scroll>
</template>

<script>
import Scroll from "./scroll";
import { getData } from "../common/tools/dom.js";

const ANCHOR_HEIGHT = 18;

export default {
  props: {
    data: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      scrollY: -1,
      currentIndex: 0 //高亮
    };
  },
  components: {
    Scroll
  },
  created() {
    // 不再data中声明是因为不需要去监测它的变化
    this.touch = {};
    this.listenScroll = true;
    this.probeType = 3;
    this.listHeight = [];
  },
  computed: {
    shortCutList() {
      return this.data.map(group => {
        return group.title.substr(0, 1);
      });
    },

    // 固定导航栏
    fixedTitle() {
      if (this.scrollY > 0) return '';
      return this.data[this.currentIndex] ? this.data[this.currentIndex].title : ''
    }
    
  },
  methods: {
    // 派发点击事件
    selectItem(item) {
      this.$emit('select',item)
    },
    refresh() {
      this.$refs.listview.refresh()
    },

    touchMoveTo(e) {
      // 更新currentIndex
      if (!e.target.getAttribute("data-index")) return;
      this.currentIndex = parseInt(e.target.getAttribute("data-index"));
      let anchorIndex = getData(e.target, "index");
      //   记录当前的y值
      let firstTouch = e.touches[0];
      this.touch.y1 = firstTouch.pageY; //记录当前Y值
      this.touch.anchorIndex = anchorIndex; //记录当前选中的元素
      this._scrollTo(anchorIndex);
    },
    onTouchMove(e) {
      let firstTouch = e.touches[0];
      this.touch.y2 = firstTouch.pageY;
      // 计算偏移了几个元素
      let delta = ((this.touch.y2 - this.touch.y1) / ANCHOR_HEIGHT) | 0;
      let anchorIndex = parseInt(this.touch.anchorIndex + delta); //重新计算选中的元素
      this._scrollTo(anchorIndex);
    },
    // 滚动到哪个元素
    _scrollTo(index) {
      this.$refs.listview.scrollToElement(this.$refs.listGroup[index], 0);
    },
    // 更新滚动的Y轴
    scroll(pos) {
      this.scrollY = pos.y;
    },
    // 计算高度
    _calculateHeight() {
      // 初始化高度
      this.listHeight = [];
      const list = this.$refs.listGroup;
      let height = 0;
      this.listHeight.push(height);
      // 累加每个group的高度
      for (let i = 0; i < list.length; i++) {
        let item = list[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    }
  },
  watch: {
    data() {
      // 数据变化到dom变化有一定的延迟
      setTimeout(() => {
        // 数据变化就计算listGroup的高度
        this._calculateHeight();
      }, 20);
    },
    scrollY(newY) {
      const listHeight = this.listHeight;
      // 当滚动到顶部  newY > 0
      if (newY > 0) {
        this.currentIndex = 0;
        return;
      }
      // 在中间部分滚动
      for (let i = 0; i < listHeight.length-1; i++) {
        let height1 = listHeight[i];
        let height2 = listHeight[i + 1];

        // 当Y轴的高度在两个元素之间   newY为负值，需要变成正数来计算
        if (-newY >= height1 && -newY < height2) {
          this.currentIndex = i;
          return;
        }
        // console.log(this.currentIndex)
      }
      // 当滚动到底部，且-newY大于最后一个元素的上限
      this.currentIndex = listHeight.length - 2;
    }
  }
};
</script>

<style lang="less" scoped>
@import "../common/style/variable.less";

.listview {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: @color-background;

  .list-group {
    padding-bottom: 30px;

    .list-group-title {
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      font-size: @font-size-small;
      color: @color-text-l;
      background: @color-highlight-background;
    }

    .list-group-item {
      display: flex;
      align-items: center;
      padding: 20px 0 0 30px;

      .avater {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }

      .name {
        margin-left: 20px;
        color: @color-text-l;
        font-size: @font-size-medium;
      }
    }
  }

  .list-shortcut {
    position: absolute;
    z-index: 30;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    padding: 20px 0;
    border-radius: 10px;
    text-align: center;
    background: @color-background-d;
    font-family: Helvetica;

    .item {
      padding: 3px;
      line-height: 1;
      color: @color-text-l;
      font-size: @font-size-small;

      &.active {
        color: @color-theme;
      }
    }
  }

  .list-fixed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;

    .fixed-title {
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      font-size: @font-size-small;
      color: @color-text-l;
      background: @color-highlight-background;
    }
  }

  .loading-container {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
}
</style>


