<template>
  <div class="song-list">
    <ul>
      <li class="item" @click="selectItem(item,index)" v-for="(item, index) in songs" :key="index">
        <!-- 添加排名 -->
        <div class="rank" v-show="rank">
          <span :class="getRankCls(index)">{{getRankText(index)}}</span>
        </div>
        <div class="content">
          <h2 class="name">{{item.name}}</h2>
          <p class="desc">{{getDesc(item)}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    songs: {
      type: Array,
      default: []
    },
    rank: {
      type: Boolean,  //用来控制是否显示排名
      default: false
    }
  },
  methods: {
    getDesc(song) {
      return `${song.singer} 。${song.album}`;
    },

    getRankCls(index) {
      if (index <= 2) {
        //给前三名添加样式
        return `icon icon${index}`
      }else {
        return 'text'
      }
    },

    selectItem(item,index) {
      this.$emit('select',item,index)
    },

    getRankText(index) {
      if (index > 2) {
        return index+1
      }
    }
  }
};
</script>


<style lang="less" scoped>
@import "../common/style/variable.less";

.song-list {
  .item {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    height: 64px;
    font-size: @font-size-medium;

    .rank {
      flex: 0 0 25px;
      width: 25px;
      margin-right: 30px;
      text-align: center;

      .icon {
        display: inline-block;
        width: 25px;
        height: 24px;
        background-size: 25px 24px;

        &.icon0 {
          background-image: url("../common/image/first@2x.png");
          @media (-webkit-min-device-pixel-ratio: 3),
            (min-device-pixel-ratio: 3) {
            background-image: url("../common/image/first@3x.png");
          }
        }
        &.icon1 {
          background-image: url("../common/image/second@2x.png");
          @media (-webkit-min-device-pixel-ratio: 3),
            (min-device-pixel-ratio: 3) {
            background-image: url("../common/image/second@3x.png");
          }
        }
        &.icon2 {
          background-image: url("../common/image/third@2x.png");
          @media (-webkit-min-device-pixel-ratio: 3),
            (min-device-pixel-ratio: 3) {
            background-image: url("../common/image/third@3x.png");
          }
        }
      }

      .text {
        color: @color-theme;
        font-size: @font-size-large;
      }
    }

    .content {
      flex: 1;
      line-height: 20px;
      overflow: hidden;

      .name {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        color: @color-text;
      }

      .desc {
        margin-top: 4px;
        color: @color-text-d;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
}
</style>

