<template>
    <div class="slider" ref="slider">
        <div class="slider-group" ref="sliderGroup">
            <slot>
                <!-- 插槽 外部的dom将会替换掉slot -->
            </slot>
        </div>
        <div class="dots">
            <span class="dot" :class="{active: currentIndex === index}" v-for="(item, index) in dots" :key="index"></span>
        </div>
    </div>
</template>

<script>
import BScroll from "better-scroll";
import { addClass } from "../common/tools/dom.js";

export default {
  props: {
    loop: {
      type: Boolean,
      default: true
    },
    autoPlay: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 3000
    }
  },
  data() {
    return {
      dots: [],     //小点点的数量
      currentIndex: 0   //当前显示第几个
    };
  },
  mounted() {
    // 由于浏览器每隔17ms刷新一次，所以这里设置20毫秒比较合适
    setTimeout(() => {
      this._setSliderWidth();
      this._initDots();
      this._initSlider();

      if (this.autoPlay) {
        this._play();
      }
    }, 20);


    // 如果窗口大小改变需要再次计算轮播图的宽度
    window.addEventListener('resize',() => {
      if (!this.slider) return;
      this._setSliderWidth(true)
      // 重新计算slider
      this.slider.refresh();
    })
  },
  methods: {
    // 设置slider的宽度
    _setSliderWidth(isResize) {
      // 获取插槽里的每个子元素  即每个包着轮播图片的盒子
      this.children = this.$refs.sliderGroup.children;

      let width = 0;
      // 获取每个盒子的实际宽度
      let sliderWidth = this.$refs.slider.clientWidth;
      // 遍历每个盒子，并且添加样式，计算出所有盒子的总长度
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i];
        addClass(child, "slider-item"); //添加样式

        child.style.width = sliderWidth + "px"; //添加长度
        width += sliderWidth;
      }
      // 如果是无缝轮播  就要添加两个盒子的宽度
      // 如果宽度再次改变就不需要在执行这段逻辑了
      if (this.loop && !isResize) {
        width += 2 * sliderWidth;
      }
      this.$refs.sliderGroup.style.width = width + "px"; //设置总长度
    },
    // 初始化小点点的数量
    _initDots() {
      // 设置一个空数组，长度为子元素的个数
      this.dots = new Array(this.children.length);
    },
    // 初始化slider
    _initSlider() {
      this.slider = new BScroll(this.$refs.slider, {
          scrollX: true,
          scrollY: false,
          momentum: false,
          snap: {
            loop: this.loop,
            threshold: 0.1
          },
        })

      this.slider.on('scrollEnd', () => {
        // 获取当前的index
        let pageIndex = this.slider.getCurrentPage().pageX;
        this.currentIndex = pageIndex;

        // 判断是否轮播
        if (this.autoPlay) {
          // 每次轮播接受后需要清除计时器，以免自动轮播和手动轮播冲突
          clearTimeout(this.timer);
          this._play()

        }
      })
    },
    // 自动播放
    _play() {
      let pageIndex = this.currentIndex + 1;
      if (this.loop) {
        var num = this.children.length - 2
      }
      if (pageIndex == num) {
        pageIndex = 0;
      }
      this.timer = setTimeout(()=>{
        this.slider.goToPage(pageIndex,0,400)
      },this.interval)
    }
  },
  destroyed() {
    // 页面销毁的时候清定时器
    clearTimeout(this.timer)
  }
};
</script>

<style lang="less" scoped>
@import "../common/style/variable.less";

.slider {
  min-height: 1px;

  .slider-group {
    position: relative;
    overflow: hidden;
    white-space: nowrap;

    .slider-item {
      float: left;
      box-sizing: border-box;
      overflow: hidden;
      text-align: center;

      a {
        display: block;
        width: 100%;
        overflow: hidden;
        text-decoration: none;

        img {
          width: 100%;
        }
      }
    }
  }

  .dots {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 12px;
    text-align: center;
    font-size: 0;

    .dot {
      display: inline-block;
      margin: 0 4px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: @color-text-l;

      &.active {
        width: 20px;
        border-radius: 5px;
        background: @color-text-ll;
      }
    }
  }
}
</style>


