<template>
  <div class="singer" v-if="singerList" ref="singer">
    <list-view @select="selectSinger" :data="singerList" ref="list"></list-view>
    <router-view> </router-view>
  </div>
</template>

<script>
import { getSingerList } from "../../api/singer.js";
import { OK } from "../../api/config.js";
import Singer from "../../common/tools/singer.js";
import ListView from "../../base/listView.vue";
// 使用vuex提供的mapMutaions,让修改mutaion的语法更简单
import {mapMutations} from 'vuex'
import {playListMixin} from '../../common/tools/mixin.js'

const HOT_NAME = "热门";
const HOT_SINGER_LEN = 10;

export default {
  mixins: [playListMixin],
  components: {
    ListView
  },
  data() {
    return {
      singerList: []
    };
  },
  mounted() {
    this._getSingerList();
  },
  methods: {
    selectSinger(singer) {
      this.$router.push({
        path: `/singer/${singer.id}`
      })
      // 修改vuex里面type为singer的值
      this.setSinger(singer)
    },
    // 获取歌手信息列表
    _getSingerList() {
      getSingerList().then(res => {
        if (res.code === OK) {
          this.singerList = this._normalizeSinger(res.data.list);
        }
      });
    },
    // 对歌手信息数据进行处理
    _normalizeSinger(list) {
      let map = {
        //  1.0热门
        hot: {
          title: HOT_NAME,
          items: []
        }
      };
      list.forEach((item, index) => {
        if (index < HOT_SINGER_LEN) {
          map.hot.items.push(
            new Singer({
              id: item.Fsinger_mid,
              name: item.Fsinger_name
            })
          );
        }

        // 2.0分类
        const key = item.Findex;
        // 没有这个分类就新建一个分类
        if (!map[key]) {
          map[key] = {
            title: key,
            items: []
          };
        }
        // 有这个分类就添加数据
        map[key].items.push(
          new Singer({
            id: item.Fsinger_mid,
            name: item.Fsinger_name
          })
        );
      });
      // 3.0  为了得到有序的数据，我们需要处理map
      let hot = [];
      let ret = [];

      for (let key in map) {
        let val = map[key];
        if (val.title.match(/[a-zA-Z]/)) {
          ret.push(val);
        } else if (val.title === HOT_NAME) {
          hot.push(val);
        }
      }
      // 默认按照字符编码从小到大排序
      // console.log('----')
      ret.sort((a, b) => {
        return a.title.charCodeAt(0) - b.title.charCodeAt(0);
      });
      return hot.concat(ret);
    },

    ...mapMutations({     //对象展开运算符
      // 映射一个方法名 可以通过this.setSinger使用和mutations.js里相对应的方法
      setSinger: 'SET_SINGER'   //这里的SET_SINGER与mutations.js里相对应
    }),

    handlePlayList(playList) {
      const bottom = playList.length > 0 ? '60px' : ''
      this.$refs.singer.style.bottom = bottom
      this.$refs.list.refresh()
    }
  }
};
</script>

<style lang="less" scoped>
.singer {
  position: fixed;
  z-index: 100;
  top: 80px;
  bottom: 0;
  width: 100%;
}
</style>


