<template>
  <div class="rank" ref="rank">
    <scroll :data="topList" class="toplist" ref="scroll">
      <ul>
        <li class="item" v-for="item in topList" @click="selectItem(item)" :key="item.id">
          <div class="icon">
            <img v-lazy="item.picUrl" width="100" height="100" alt="">
          </div>
          <ul class="songlist">
            <li class="song" v-for="(song, index) in item.songList" :key="index">
              <span>{{index+1}} </span>
              <span>{{song.singername}}-{{song.songname}}</span>
            </li>
          </ul>
        </li>
      </ul>
      <div class="loading-container" v-show="!topList.length">
        <loading></loading>
      </div>
    </scroll>

    <router-view></router-view>
  </div>
</template>

<script>
import { getTopList } from "../../api/rank.js";
import { OK } from "../../api/config.js";
import Scroll from "../../base/scroll";
import Loading from "../../base/loading";
import {mapMutations} from 'vuex'
import {playListMixin} from '../../common/tools/mixin.js'

export default {
  mixins: [playListMixin],
  data() {
    return {
      topList: []
    };
  },
  components: {
    Scroll,
    Loading
  },
  created() {
    this._getTopList();
  },
  methods: {
    ...mapMutations({
      setTopList: 'SET_TOP_LIST'
    }),

    // 获取排行榜数据
    _getTopList() {
      getTopList().then(res => {
        // console.log(res)
        if (res.code === OK) {
          // console.log(res.data.topList)
          this.topList = res.data.topList;
        }
      });
    },

    // 跳转到详情页面
    selectItem(item) {
      this.$router.push({
        path: `/rank/${item.id}`
      })
      // 保存数据到vuex
      this.setTopList(item)
    },

    handlePlayList(playList) {
      let bottom = playList.length > 0 ? '60px' : ''
      this.$refs.rank.style.bottom = bottom
      this.$refs.scroll.refresh()
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../common/style/variable.less";

.rank {
  position: fixed;
  width: 100%;
  top: 88px;
  bottom: 0;

  .toplist {
    height: 100%;
    overflow: hidden;

    .item {
      display: flex;
      margin: 0 20px;
      padding-top: 20px;
      height: 100px;

      &:last-child {
        padding-bottom: 20px;
      }

      .icon {
        flex: 0 0 100px;
        width: 100px;
        height: 100px;
        border: 1px solid #fff;
      }

      .songlist {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 20px;
        height: 100px;
        overflow: hidden;
        background: @color-highlight-background;
        color: @color-text-d;
        font-size: @font-size-small;

        .song {
          line-height: 26px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }
      }
    }

    .loading-container {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>




