<template>
    <div class="music-list">
        <div class="back" @click="back"></div>
        <h1 class="title" v-html="title"></h1>
        <div class="bg-image" :style="bgStyle" ref="bgImage">
            <div class="play-wrapper">
              <div class="play" v-show="songs.length > 0" ref="playBtn">
                <i class="icon-play"></i>
                <span class="text">随机播放全部</span>
              </div>
            </div>
            <div class="filter" ref="filter"></div>
        </div>
        <!-- 用来遮住背景图片，高度会动态变化 -->
        <div class="bg-layer" ref="layer"></div>

        <scroll @scrollMove="scroll" :data="songs" :probe-type="probeType" :listen-scroll="listenScroll" class="list" ref="list">
            <div class="song-list-wrapper">
                <song-list @select="selectItem" :rank="rank" :songs="songs"></song-list>
            </div>

            <!-- loading -->
            <div class="loading-container" v-show="!songs.length">
              <loading></loading>
            </div>
        </scroll>
    </div>
</template>

<script>
import Scroll from "../../base/scroll"
import SongList from "../../base/songList.vue"
import Loading from "../../base/loading"
// 给浏览器添加兼容前缀
import {prefixStyle} from "../../common/tools/dom.js"
import {mapActions} from "vuex"
import {playListMixin} from '../../common/tools/mixin.js'

const RESERVED_HEIGHT = 40  //预留的高度
const transform = prefixStyle('transform')
const backdrop = prefixStyle('backdrop-filter')

export default {
  mixins: [playListMixin],
  props: {
    bgImage: {
      type: String,
      default: ""
    },
    songs: {
      type: Array,
      default: []
    },
    title: {
      type: String,
      default: ""
    },
    rank: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      scrollY : 0
    }
  },
  components: {
      Scroll,
      SongList,
      Loading
  },
  created() {
      // 实时监听scroll
      this.probeType = 3;
      this.listenScroll = true
  },
  mounted() {
    // 记录图片的高度
    this.imageHeight = this.$refs.bgImage.clientHeight
    this.minTranslateY = -this.imageHeight + RESERVED_HEIGHT
    //这里的list是vue里的一个对象，需要用$el转成dom对象
    this.$refs.list.$el.style.top = `${this.imageHeight}px`
  },
  methods: {
    handlePlayList(playList) {
      const bottom = playList.length > 0 ? '60px' : ''
      this.$refs.list.$el.style.bottom = bottom
      this.$refs.list.refresh()
    },
    scroll(pos) {
      this.scrollY = pos.y
    },
    back() {
      this.$router.back()
    },

    selectItem(item,index) {
      this.selectPlay({
        list: this.songs,
        index
      })
    },

    ...mapActions([
      'selectPlay'
    ])
  },
  watch: {
    scrollY(newY) {
      let translateY = Math.max(this.minTranslateY,newY)  //取两者之间的最大值
      let zIndex = 0;
      let scale = 1;
      let blur = 0;
      // 动态更改layer的高度
      this.$refs.layer.style[transform] = `translate3d(0,${translateY}px,0)`

      const percent = Math.abs( newY / this.imageHeight)
      if (newY > 0) {
        // 下拉时放大图片
        scale = 1 + percent
        zIndex = 10
      }else {
        // 设置图片逐渐模糊
        blur = Math.min(20 * percent, 20)  //取最小值，默认最大20
      }
      //模糊
      this.$refs.filter.style[backdrop] = `blur(${blur}px)`

      if (newY < translateY) {
        // 向上移动的距离大于预定的高度
        zIndex = 10;
        this.$refs.bgImage.style.paddingTop = 0;
        this.$refs.bgImage.style.height = RESERVED_HEIGHT+'px'
        this.$refs.playBtn.style.display = 'none'
      }else {
        // 变回原来的样式
        this.$refs.bgImage.style.paddingTop = '70%';
        this.$refs.bgImage.style.height = 0
        this.$refs.playBtn.style.display = 'block'
      }
      this.$refs.bgImage.style.zIndex = zIndex;
      
      // 下拉时图片放大
      this.$refs.bgImage.style[transform] = `scale(${scale})`
      
    } 
  },

  computed: {
    // 背景图片
    bgStyle() {
        return `background-image: url(${this.bgImage})`
    }
  }
};
</script>

<style lang="less" scoped>

@import "../../common/style/variable.less";

.music-list {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: @color-background;

  .back {
    position: absolute;
    top: 5px;
    left: 12px;
    z-index: 50;
    width: 26px;
    height: 26px;
    background-image: url("../../common/image/back.png");
    background-size: 100%;

  }

  .title {
    position: absolute;
    top: 0;
    left: 10%;
    z-index: 40;
    width: 80%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    text-align: center;
    line-height: 40px;
    font-size: @font-size-large;
    color: @color-text;
  }

  .bg-image {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 70%;
    transform-origin: top;
    background-size: cover;

    .play-wrapper {
      position: absolute;
      bottom: 20px;
      z-index: 50;
      width: 100%;

      .play {
        box-sizing: border-box;
        width: 135px;
        padding: 7px 0;
        margin: 0 auto;
        text-align: center;
        border: 1px solid @color-theme;
        color: @color-theme;
        border-radius: 100px;
        font-size: 0;

        .icon-play {
          display: inline-block;
          width: 20px;
          height: 20px;
          background-image: url("../../common/image/play.png");
          background-size: 100%;

          vertical-align: middle;
          margin-right: 6px;
        }

        .text {
          display: inline-block;
          vertical-align: middle;
          font-size: @font-size-small;
        }
      }
    }

    .filter {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(7, 17, 27, 0.4);
    }
  }

  .bg-layer {
    position: relative;
    height: 100%;
    background: @color-background;
  }

  .list {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    background: @color-background;

    .song-list-wrapper {
      padding: 20px 30px;
    }

    .loading-container {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>

