<template>
    <transition name="slide">
        <music-list :title="title" :bgImage="bgImage" :songs="songsData"></music-list>
    </transition>
</template>

<script>
import MusicList from "../music-list/music-list";
import { mapGetters } from "vuex";
import songList from "../../data/songList.json";

export default {
  components: {
    MusicList
  },
  computed: {
    ...mapGetters(["disc"]),
    title() {
      return this.disc.dissname;
    },

    bgImage() {
      return this.disc.imgurl;
    },

    songsData() {
      let songs = [];
      let songsList = songList.cdlist[0].songlist;
      for (let i = 0; i < songsList.length; i++) {
        let obj = {};
        obj.name = songsList[i].title;
        obj.singer = songsList[i].singer[0].name;
        obj.album = songsList[i].album.name;
        obj.image = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${songsList[i].singer[0].mid}.jpg?max_age=2592000`
        obj.duration = 215
        obj.url = "http://dl.stream.qqmusic.qq.com/C400003OUlho2HcRHC.m4a?guid=1546862306&vkey=E64D3C27D1583641B0AD8240C9AC45BF9D274B016C4CB8070BD3019584FAEC049A8D5674AF3BA932384614688F2C0A215C43C7B92C9DD206&uin=0&fromtag=38"
        songs.push(obj);
      }

      return songs;
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../common/style/variable.less";

.slide-enter-active,
.slide-leave-active {
  transition: all 0.3s;
}

.slide-enter,
.slide-leave-to {
  transform: translate3d(100%, 0, 0);
}
</style>
