<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>

<script>
import BScroll from "better-scroll";

export default {
  props: {
    probeType: {    //一共三种类型 1 不会派发滚动事件  2 只有手指滑动时触发事件 3 滚动就触发
      type: Number,
      default: 1
    },
    click: {     
      type: Boolean,
      default: true
    },
    data: {       //数据  必需
      type: Array,  
      default: null
    },
    listenScroll: { //监听滚动事件
      type: Boolean,
      defaule: false
    },
    pullup: {     //上拉刷新
      type: Boolean,
      default: false
    }
  },
  mounted() {
    setTimeout(() => {
      this._initScroll();
    }, 20);
    
  },
  methods: {
    _initScroll() {
      if (!this.$refs.wrapper) return;
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click
      });

      if (this.listenScroll) {
        let that = this;
        this.scroll.on("scroll", pos => {
          // 向外部派发一个scrollMove事件，通过event获取传递的数据
          that.$emit("scrollMove", pos);
        });
      }

      if (this.pullup) {
        // 调用betterScroll里的scrollEnd事件  滚动结束时触发
        this.scroll.on('scrollEnd', ()=>{
          if (this.scroll.y <= this.scroll.maxScrollY + 50) {
            // 派发一个滚动到底部的事件
            this.$emit('scrollToEnd')
          }
        })
      }
    },
    // 启用better-scroll
    enable() {
      this.scroll && this.scroll.enable();
    },
    // 禁用better-scroll
    disable() {
      this.scroll && this.scroll.disable();
    },
    // 数据更新时重新计算
    refresh() {
      this.scroll && this.scroll.refresh();
    },
    // 移动到
    scrollTo() {
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
    // 移动到某个元素
    scrollToElement() {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
    },
    // 滚动结束
    scrollEnd() {
      this.scroll && this.scroll.scrollEnd()
    }
  },
  watch: {
    data() {
      setTimeout(() => {
        this.refresh();
      }, 20);
    }
  }
};
</script>




