<template>
  <div class="search">
    <div class="search-box-wrapper">
      <SearchBox ref="searchBox" @query="queryChange"></SearchBox>
    </div>

    <div class="shortcut-wrapper">
      <div class="shorcut">
        <div class="hot-key">
          <h1 class="title">热门搜索</h1>
          <ul>
            <li @click="addQuery(item.k)" class="item " v-for="item in hotkey" :key="item.n">
              <span>{{item.k}}</span>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="search-result" v-show="query.length > 0" ref="searchResult">
      <Suggest :query="query" ref="suggest"></Suggest>
    </div>

    <router-view>s</router-view>
  </div>
</template>

<script>
import SearchBox from "../../base/searchBox";
import { getHotKey } from "../../api/search.js";
import { OK } from "../../api/config.js";
import Suggest from "../suggest/suggest";
import {playListMixin} from '../../common/tools/mixin.js'

export default {
  mixins: [playListMixin],
  components: {
    SearchBox,
    Suggest
  },
  data() {
    return {
      hotkey: [],
      query: ""
    };
  },
  created() {
    this._getHotKey();
  },

  methods: {
    // 获取热门搜索数据
    _getHotKey() {
      getHotKey().then(res => {
        // console.log(res)
        if (res.code === OK) {
          // console.log(res.data.hotkey);
          this.hotkey = res.data.hotkey.slice(0, 10);
        }
      });
    },

    addQuery(query) {
      // 调用searchBox组件里的setQuery方法，把值传递过去
      this.$refs.searchBox.setQuery(query);
    },

    queryChange(query) {
      // 接受searchBox传来的query值
      this.query = query;
    },

    handlePlayList(playList) {
      let bottom = playList.length > 0 ? '60px' : ''
      this.$refs.searchResult.style.bottom = bottom
      this.$refs.suggest.refresh()
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../common/style/variable.less";

.search {
  .search-box-wrapper {
    margin: 20px;
  }

  .shortcut-wrapper {
    position: fixed;
    top: 178px;
    bottom: 0;
    width: 100%;

    .shortcut {
      height: 100%;
      overflow: hidden;
    }
    .hot-key {
      margin: 0 20px 20px 20px;

      .title {
        margin-bottom: 20px;
        font-size: @font-size-medium;
        color: @color-text-l;
      }

      .item {
        display: inline-block;
        padding: 5px 10px;
        margin: 0 10px 10px 0;
        border-radius: 6px;
        background: @color-highlight-background;
        font-size: @font-size-medium;
        color: @color-text-d;
      }
    }
  }

  .search-result {
    position: fixed;
    background: #222;
    width: 100%;
    top: 178px;
    bottom: 0;
  }
}
</style>


