<template>
    <transition name='slide'>
        <music-list :title="title" :bg-image="bgImage" :songs="songList" :rank="true"></music-list>
    </transition>
</template>


<script>
import MusicList from '../music-list/music-list';
import {mapGetters} from "vuex"
import {getMusicList} from '../../api/rank.js'
import {OK} from '../../api/config.js'

export default {
    components: {
        MusicList
    },
    data() {
        return {
            songList: []
        }
    },
    computed: {
        ...mapGetters([
            'topList'
        ]),
        title() {
            return this.topList.topTitle
        },
        bgImage() {
            return this.topList.picUrl
        }
    },
    created() {
        this._getMusicList()
    },
    methods: {
        _getMusicList() {
            getMusicList(this.topList.id).then(res=> {
                if (res.code === OK) {
                    // this.songList = res.songlist
                    let songs = []
                    // 对返回的数据进行处理
                    res.songlist.forEach(item=>{
                        let obj = {}
                        obj.name = item.data.songname
                        obj.singer = item.data.singer[0].name
                        obj.album = item.data.albumname
                        obj.image = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${item.data.singer[0].mid}.jpg?max_age=2592000`
                        obj.duration = 215
                        obj.url = 'http://dl.stream.qqmusic.qq.com/C400003OUlho2HcRHC.m4a?guid=1546862306&vkey=E64D3C27D1583641B0AD8240C9AC45BF9D274B016C4CB8070BD3019584FAEC049A8D5674AF3BA932384614688F2C0A215C43C7B92C9DD206&uin=0&fromtag=38'
                        songs.push(obj)
                    })
                    this.songList = songs
                }
            })
        },
    }

}
</script>

<style lang="less" scoped>

.slide-enter-active,.slide-leave-active {
    transition: all 0.3s
}

.slide-enter,.slide-leave-to {
    transform: translate3d(100%,0,0)
}

</style>

